



































export default {
	data() {
		return {
		   curindex:1,
		   overdue:'N'
		};
	},
	methods:{
		choseyuqi:function(num){
			console.log(num)
			this.curindex = num
			if(num == 1){
				this.overdue = 'N'
			}else{
				this.overdue = 'Y'
			}
		},
		sureshuaixuan:function(){
			uni.setStorageSync('zhandianisOverdue',this.overdue)
			uni.navigateBack({
				delta:1
			})
		}
	}
}
